
.navbar{
	width:100%;
	height:49px;
	position: absolute;
	left:0;
	bottom:0;
	z-index:10;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background: #f8f8f8;
}
.navbar:before{
	content:'';
	display: block;
	height:1px;
	width:100%;
	border-top:1px solid #CCCCCC;
	position:absolute;
	top:-1px;
	left:0;
	-webkit-transform: scaleY(0.5);
	        transform: scaleY(0.5);
}
.navbar a{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	text-align: center;
	color: #666;
	position: relative;
}
.navbar a:last-child .weui-badge{
	display: none;
}
.navbar .iconfont{
	display: block;
	width: 100%;
	text-align: center;
	color: #666;
	margin-top:4px;
}
.navbar a.router-link-active .iconfont{
	color: #10AEFF;
}
.navbar a.router-link-active p{
	color: #10AEFF;
}
.page{
	width:100%;
	position: absolute;
	left:0;
	top:0;
	bottom:49px;
}

#td{
		overflow: hidden;
}
.list{	
	width:100%;
}
.list li{
	border-bottom:1px solid #e5e5e5;
	padding:10px  15px;
	overflow: hidden;
	position: relative;
	color:lightslategrey
}
li .task_name{
	display:block;
	font-size:16px;
	color: #666666;	
	margin-bottom: 3px;
}
.do_user{
	display: inline-block;
	font-size:12px;
	margin-bottom: 3px;
}
.status{
	padding: 1px 2px;
	display: inline-block;
	text-align: center;
	background-color:#10AEFF;
	color:#fff
}
.status.near{
	background-color:#FFBE00 ;
}
.status.over{
	background-color:#F76260;
}
.code{
	display: inline-block;
	color:#D2D2D2;
}
.date{
	position: absolute;
	right:15px;
	top:20px;
}
.bottom{
	width:100%;
	height:30px;
	line-height: 30px;
	text-align: center;
}

#finish{
	overflow: hidden;
}
#finish .status{
	background-color:#09BB07 ;
}
	

#td_detail{
	width:100%;
	position:absolute;
	left:0;
	top:0;
	bottom:0;
	z-index:100;	
	background: #fff;
}
#td_detail button{
	margin:5px 0 0 15px;
}
.detail{
	width:100%;
	border-bottom: 1px solid #ccc;
	padding: 5px 15px;
}
.detail p{
	line-height: 30px;
	color:lightslategrey;
}
p.title{
	font-size:16px;
	color:#000;
	font-weight: 700;
}
p.status1{
	width:50px;
	line-height: 20px;
	padding: 2px 0;
	text-align: center;
	color:#fff ;
	background:#10AEFF ;
}
#td_detail ul{
	border-bottom: 1px solid #ccc;
	padding: 15px;
}
#td_detail ul li{
	line-height: 30px;
}
ul li span{
	color:lightslategrey;
}
ul li i{
	color:#000;
}
.btn{
	width:100%;
	position: absolute;
	left:0;
	bottom: 10px;
}
.process{
	width:100%;
}
.process .agree_status{	
	margin:15px;
}
.agree_status .info{
	border:1px solid #ccc;
	padding: 10px;
	position: relative;
	margin-top:30px;
	vertical-align: middle;
}
.info .iconfont{
	position: absolute;
	top:12px;
}
.info p{
	display: inline-block;
	margin-left:30px;
}
.info p span{
	display: block;
}
.create_date{
	position: absolute;
	right:10px;
	top:3px;
}
.weui-icon{
	position:absolute;
	right:25px;
	top:28px;
}
